import React from 'react'
import './Details.css'

const Details = (props) => {
    return (
        <div class="container">
            {/* 返回 */}
            <button class="btn-back" onClick={()=>props.history.push('/')} > 
                <i className="iconfont icon-jiantouzuo"></i> 
            </button>
            {/* 商品详情 */}
            <div class='box'>
                <img class="details-img" src={`${props.location.state}`} />
                <div class="price-box1">
                    <span class="price-h1">￥</span>
                    <span class="price1">69</span>
                    <span class="payed1">830人已购买</span>
                </div>
                <div class="title1">
                    <span class="title-p1">忠犬小八梨形自制-格子衬衫-2020年春季新款休闲百搭衬衫</span>
                </div>
            </div>
            {/* 底部栏 */}
            <div class='bar'>
                {/* 店铺 */}
                <div class="shop bar1">
                    <p><i className="iconfont icon-icon_A"></i></p>
                    <p class="bar-text">店铺</p>
                </div>
                {/* 客服 */}
                <div class="wang bar1">
                    <p><i className="iconfont icon-aliwangwang-aliwangwang"></i></p>
                    <p class="bar-text">客服</p>
                </div>
                {/* 收藏 */}
                <div class="fav bar1">
                    <p><i className="iconfont icon-shoucang"></i></p>
                    <p class="bar-text">收藏</p>
                </div>
                {/* 加入购物车|购买 */}
                <div class="buy">
                    <button class="buy1 btn-lg btn-left">加入购物车</button>
                    <button class="buy1 btn-lg btn-right">立即购买</button>
                </div>
            </div>

        </div>
    )
}

export default Details
