import Mytab from './Mytab';
import Home from './Home';
import Cart from './Cart';
import My from './My';
import Order from './Order';
import SearchBarTop from './SearchBarTop';
import Details from './Details'

const routers=[
      {
        path:'/',
        exact:true,
        component:Home
      },
      {
        path:'/my',
        component:My
      },
      {
        path:'/cart',
        component:Cart
      },
      {
        path:'/order',
        component:Order
      },
      {
        path:'/search',
        component:SearchBarTop 
      },
      {
        path:'/details',
        component:Details 
      }
    ]
    
    export default routers;