import Item from 'antd/lib/list/Item'
import React ,{Component}from 'react'
import { BrowserRouter as Router, Route, NavLink, Switch ,withRouter} from 'react-router-dom'
import './Commodity.css'
import goods from './goods'


class Commodity extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        return (
            <div class='container'>
                {/*商品概览信息*/}
                {goods.map((i) => (
                <div onClick={()=>{this.props.history.push('/details',`${i.src}`)}}>
                    <div class="item">
                        <a class='item-1'>
                            <img class="item-img" src={`${i.src}`} alt={`${i.alt}`} />
                        </a>
                        <a class="item-2">
                            <div class="title">
                                <span class="title-p">{`${i.title}`}</span>
                            </div>
                            <div class="price-box">
                                <span class="price-h">￥</span>
                                <span class="price">{`${i.price}`}</span>
                                <span class="payed">{`${i.payed}`}人已购买</span>
                            </div>
                        </a>
                    </div>
                </div>
                ))}
            </div>
        )
    }
}
export default withRouter(Commodity)
